        ret[i] = {\n            show: show,\n            list: t\n          }\n        }\n      }\n      this.list = ret\n    }\n  },\n  methods: {\n    select() {\n      $(window).scrollTop(0)\n    },\n    handleToggle (item) {\n      item.show = !item.show\n    },\n    change (value) {\n      this.component = value\n      window.location.hash = value\n    },\n    init () {      \n      var hash = window.location.hash\n      hash = hash.substr(1, hash.length)\n      if (hash) {\n        this.component = hash\n        setTimeout(() => {\n          prismjs.highlightAll()\n          document.querySelectorAll('table').forEach(i => {\n            i.classList.add('table', 'table-bordered')\n          })\n        })\n      }\n    }\n  },\n  mounted () {\n    this.list = this.map\n    this.init()\n    window.onhashchange = this.init\n    setTimeout(() => {\n      $(window).scrollTop(0)\n      prismjs.highlightAll()\n      document.querySelectorAll('table').forEach(i => {\n        i.classList.add('table', 'table-bordered')\n      })\n    })\n  },\n  components: {\n    headerDocs,\n    n3LayoutDocs,\n    n3AlertDocs,\n    n3AccordionDocs,\n    n3IconDocs,\n    n3AffixDocs,\n    n3SwitchDocs,\n    n3AsideDocs,\n    n3ToastDocs,\n    n3CarouselDocs,\n    n3InputDocs,\n    n3TextareaDocs,\n    n3ButtonsDocs,\n    n3DatepickerDocs,\n    n3TimepickerDocs,\n    n3RateDocs,\n    n3DatetimepickerDocs,\n    n3InputNumberDocs,\n    n3UploaderDocs,\n    n3TreeDocs,\n    n3DropdownDocs,\n    n3CardDocs,\n    n3ModalDocs,\n    n3PopoverDocs,\n    n3SelectDocs,\n    n3RadioDocs,\n    n3CheckboxDocs,\n    n3TabsDocs,\n    n3TooltipDocs,\n    n3TypeaheadDocs,\n    n3StepDocs,\n    n3NavDocs,\n    n3ProgressbarDocs,\n    n3TimelineDocs,\n    n3LoadingDocs,\n    n3PageDocs,\n    n3SliderDocs,\n    n3CascaderDocs,\n    n3MultipleInputDocs,\n    n3DataTableDocs,\n    n3BreadcrumbDocs,\n    n3FormDocs,\n    n3TagsDocs,\n    n3AnimateDocs\n  }\n}\n</script>\n\n<style>\n  .bs-docs-sidenav a:hover{